.card{background-color:#fff;border-radius:1rem;padding:1rem;max-width:1000px;width:100%;box-shadow:0 4px 6px rgba(0,0,0,.1);border:1px solid #e2e8f0;display:flex;flex-direction:row;align-items:center;gap:1.5rem;margin-left:auto}.header{display:flex;align-items:center;gap:.75rem;margin-bottom:0}.logo{width:2rem;height:2rem;object-fit:contain}.title{font-size:1.25rem;font-weight:bold;white-space:nowrap}.selectContainer{display:flex;align-items:center}.selectBox{padding:.4rem .6rem;font-size:.9rem;border-radius:.5rem}.details{display:flex;flex-direction:row;color:#374151;font-size:.875rem;gap:.25rem}.error{color:red}